@import url(https://fonts.googleapis.com/css2?family=Shanti&display=swap);
:root {
  --primary: #ff8585;
  --secondary: #e43e5c;
  --accent: #5ae781;
  --white: #ffeeee;
  --black: #141414;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 100vw;
  padding: 0.2em 1em;
  background: var(--secondary);
  box-shadow: 0px 0.25rem 1.25rem rgba(0, 0, 0, 0.25);
}
.nav-item {
  margin: 0 3em;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}
.nav-item * {
  color: var(--primary);
}
.nav-item:hover {
  filter: brightness(85%);
}
.logo-con {
  font-size: 1.4rem;
  color: var(--black);
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 0.3rem;
  gap: 0.3rem;
}
.logo {
  width: 3rem;
  height: 3rem;
}
.nav-icon {
  height: 1.875rem;
  width: 1.875rem;
}
.nav-item-links img {
  display: none;
}
.nav-link {
  text-transform: capitalize;
  font-weight: 300;
  font-size: 1.4rem;
}
.nav-search-from {
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-input {
  width: 21.25rem;
  height: 1.875rem;
  border-radius: 1.25rem 0 0 1.25rem;
}
.nav-search-from input[type="text"] {
  background: #fff;
  border: none;
  text-align: center;
}
.nav-search-from input[type="text"]:focus-visible {
  border: none;
}

.search-submit {
  height: 2rem;
  width: 1.25rem;
}

.search-btn {
  height: 2rem;
  width: 2.5rem;
  border: none;
  background: #fff;
  transition: all 0.5s ease-in-out;
  border-radius: 0 1.25rem 1.25rem 0;
  cursor: pointer;
}
.search-btn:hover {
  filter: brightness(350%);
}

footer {
  position: relative;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.2em 1em;
  background: #dff1fd;
  grid-gap: 25em;
  gap: 25em;
}
.foot-item {
  font-weight: 300;
  font-size: 1.25rem;
  letter-spacing: 0.2em;
}
.foot-center {
  text-align: center;
  font-weight: normal;
  font-size: 1.5rem;
}
.foot-center .foot-top {
  text-transform: uppercase;
}
.foot-center a {
  color: var(--accent);
}
.foot-left,
.foot-right {
  cursor: pointer;
  text-transform: capitalize;
}

.foot-left {
  text-align: left;
}
.foot-right {
  flex-direction: column;
  text-align: right;
}

/* home top css */

.home-top {
  position: relative;
  bottom: 2em;
  z-index: 2;
  height: 698px;
  width: 100vw;
  background: var(--primary);
  display: flex !important;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-shadow: inset 0px -5px 10px rgba(0, 0, 0, 0.1);
}
.home-top-card-img {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 1398px;
  height: 284px;
  border-radius: 1.25rem;
  transition: all 1s ease-in-out;
}
.home-top-card-img:hover {
  transform: translateY(-1em);
}

.features {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 3em;
  gap: 3em;
  margin-bottom: 5em;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.feature {
  padding: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 302px;
  height: 333px;
  border: 1px solid var(--accent);
  box-sizing: border-box;
  border-radius: 20px;
  transition: all 0.5s ease-in-out;
  grid-gap: 1em;
  gap: 1em;
}
.feature:hover {
  box-shadow: inset 2px 4px 34px rgba(90, 231, 129, 0.5);
}
.feature * {
  text-align: center;
  margin: 0;
}
.feature h5 {
  font-size: 30px;
  color: var(--accent);
  text-transform: capitalize;
}
.feature p {
  font-size: 20px;
  line-height: 32px;
  color: var(--primary);
}

/* home-mid css */
.home-mid {
  grid-template-columns: repeat(7, 0fr) !important;
  grid-gap: 6rem !important;
  gap: 6rem !important;
}
.home-mid-card {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
.home-mid-card:hover {
  transform: translateY(-0.5em);
}
.home-mid-card img {
  width: 150px;
  height: 150px;
  box-shadow: 0px 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
  position: absolute;
  z-index: 1;
  border-radius: 1.25rem;
  transition: all 0.5s ease-in-out;
  filter: blur(1px);
}
.home-mid-card img:hover {
  filter: blur(0);
  z-index: 3;
}
.home-mid-card h4 {
  width: 100px;
  font-size: 1.5rem;
  text-transform: capitalize;
  text-align: center;
  z-index: 2;
  color: var(--white);
  transition: all 0.5s ease-in-out;
}

/* home genre css*/
.home-genre {
  width: 100vw;
  box-shadow: inset 0px 0px 15px 5px rgba(0, 0, 0, 0.2);
  background: var(--primary);
  justify-items: center;
  padding: 3em 0;
}
.home-genre-card {
  width: 25rem;
  height: 12.5rem;
  box-shadow: 0px 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
  border-radius: 1.25rem;
  transition: all 0.5s ease-in-out;
}
.home-genre-card:hover {
  transform: translateY(0.5em);
  box-shadow: none;
}
.home-genre-img {
  width: 25rem;
  height: 12.5rem;
  border-radius: 1.25rem;
}

.home-genre-card-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  bottom: 12.5rem;
  color: #fff;
  font-weight: 300;
  font-size: 2.5rem;
  text-transform: capitalize;
  width: 25rem;
  height: 12.5rem;
  transition: all 0.5s ease-in-out;
  border-radius: 1.25rem;
}
.home-genre-card-text:hover {
  -webkit-backdrop-filter: blur(0.25rem);
          backdrop-filter: blur(0.25rem);
}
.home-genre-card-title {
  font-weight: normal;
  font-size: 3.125rem;
}

.help {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-gap: 1em;
  gap: 1em;
  margin-top: 5em;
  margin-bottom: 6em;
}
.help h4 {
  color: var(--secondary);
  font-size: 30px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  transition: all 0.5s ease-in-out;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
}
.help h4:hover {
  border-bottom: 2px solid var(--secondary);
}
.help h3 {
  font-size: 60px;
  line-height: 76px;
  letter-spacing: 0.1em;
  text-transform: capitalize;
  color: var(--accent);
  width: 60%;
  text-align: center;
  margin: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: all 0.2s ease-in-out;
}
.help h3:hover {
  letter-spacing: 0.2em;
}
.help p {
  font-weight: normal;
  font-size: 20px;
  letter-spacing: 0.1em;
  color: var(--primary);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.help-cards {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5em;
  gap: 5em;
}
.help-cards-card {
  padding: 3em;
  width: 425px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  background: var(--primary);
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  grid-gap: 2em;
  gap: 2em;
  transition: all 0.7s ease-in-out;
}
.help-cards-card * {
  margin: 0;
}
.help-cards-card h5 {
  font-size: 20px;
  letter-spacing: 0.7em;
  text-transform: uppercase;
  color: var(--white);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.help-cards-card p {
  font-size: 30px;
  letter-spacing: 0.1em;
  color: var(--black);
}
.help-cards-card:hover {
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.5);
  transform: translateY(-1em);
}

.me {
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "made with with with"
    "pic name name name"
    "pic bio bio bio"
    "pic btn1 btn2 btn3";
  justify-items: center;
  align-items: center;
  background: var(--secondary);
  box-shadow: inset 0px 0px 15px 5px rgba(0, 0, 0, 0.4);
  padding: 5em;
}
.me * {
  margin: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.me h5 {
  font-size: 20px;
  grid-area: with;
  letter-spacing: 0.1em;
  text-transform: capitalize;
  color: var(--primary);
}
.me h3 {
  grid-area: made;
  font-size: 30px;
  letter-spacing: 0.1em;
  text-transform: capitalize;
  color: var(--white);
}
.me h4 {
  grid-area: name;
  font-size: 50px;
  letter-spacing: 0.4em;
  text-transform: capitalize;
  color: var(--accent);
}
.me-pic {
  grid-area: pic;
}
.me img {
  border-radius: 100%;
  width: 288px;
  height: 288px;
  border: 1px solid var(--accent);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.3));
  transition: all 0.5s ease-in-out;
}
.me img:hover {
  transform: translateY(-1em);
}
.me p {
  grid-area: bio;
  width: 80%;
  font-size: 30px;
  line-height: 100%;
  letter-spacing: 0.1em;
  text-align: center;
  text-transform: capitalize;
  color: var(--primary);
}
.me-btn {
  width: 330px;
  mix-blend-mode: normal;
  border-radius: 40px;
  background: var(--primary);
  color: var(--accent);
  font-size: 30px;
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  border: none;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  padding: 0.5em 0;
}
.me-btn:hover {
  transform: translateY(-1em);
}
.me-btn-1-con {
  grid-area: btn1;
}
.me-btn-2 {
  background: var(--accent);
  color: var(--primary);
}
.me-btn-2:hover {
  transform: translateY(1em);
}
.me-btn-2-con {
  grid-area: btn2;
}
.me-btn-3-con {
  grid-area: btn3;
}

.cards-title {
  font-weight: normal;
  font-size: 50px;
  width: 90vw;
  text-align: left;
}
.cards-con {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 4rem;
  gap: 4rem;
}

.card {
  width: 286px;
  height: 421px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: all 0.5s ease-in-out;
  overflow: hidden;
}
.card:hover {
  transform: translateY(-0.5em);
}
.card * {
  transition: all 0.5s ease-in-out;
}
.card-img {
  width: 286px;
  height: 421px;
  position: absolute;
  z-index: 1;
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}
.card-img:hover {
  z-index: 3;
}
.card-text {
  width: 286px;
  height: 200px;
  z-index: 2;
  font-weight: normal;
  font-size: 1.875rem;
  text-align: center;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 0 0 20px 20px;
}
.card-text:hover {
  height: 421px;
}
.card-title {
  color: var(--black);
}
.card-price {
  color: var(--secondary);
}
.card-discount {
  color: var(--accent);
}

/* home containers css */
.home-con {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 4rem;
  gap: 4rem;
  margin-bottom: 2em;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 565px) {
  /* nav bar css*/
  nav {
    display: grid;
    grid-template-columns: repeat(5, 2fr);
    height: auto;
    max-width: 97vw;
    justify-items: center;
    align-items: center;
    padding: 0.5em;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }
  .nav-item {
    margin: 0;
    width: 1.875rem;
  }
  .logo-con {
    font-size: 0px;
  }
  .logo {
    width: 3rem;
    height: 3rem;
  }
  .nav-item-links img {
    display: flex;
  }
  .nav-item-links .nav-link {
    display: none;
  }
  /* search bar css */
  .search-bar {
    order: 6;
    width: 4rem;
  }
  .search-btn {
    height: 2rem;
  }
  /* body css */
  .container {
    width: 100vw;
    padding: 2em 0 0 0 !important;
    overflow: hidden;
  }
  /* home genreal css*/
  .home-con {
    grid-template-columns: repeat(1, 0fr);
    justify-items: center;
  }
  /* home top css*/
  .home-top {
    grid-gap: 0;
    gap: 0;
    height: auto;
    padding: 5em 0;
  }
  .home-top-card {
    width: 90vw;
    height: 15rem;
  }
  .home-top-card-img {
    height: 9rem;
    width: 90vw;
  }
  /* home features css */
  .features {
    flex-direction: column;
  }
  /* home authors css*/
  .home-mid {
    grid-template-columns: repeat(2, 1fr) !important;
    justify-items: center;
    margin-bottom: 5em;
    grid-gap: 7em !important;
  }
  .home-mid-card {
    height: 4rem;
    width: 4rem;
    font-size: 1rem;
  }
  /* genre css */
  .home-genre-card,
  .home-genre-card img,
  .home-genre-card-text {
    width: 22rem;
  }
  /* cards css*/
  .cards-con {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2em;
    gap: 2em;
  }
  /* card css */
  .card {
    width: 11rem;
    height: 17.8rem;
  }
  .card img {
    width: 11rem;
    height: 17.8rem;
  }
  .card-text {
    width: 10rem;
    padding: 0 0.5rem;
  }
  .card-title {
    font-size: 1.5rem;
  }
  /* help css */
  .help h4 {
    font-size: 1.3em;
  }
  .help h3 {
    font-size: 2em;
    margin: 0;
    width: 70%;
  }
  .help h3:hover {
    width: 80%;
  }
  .help p {
    text-align: center;
    width: 70%;
  }
  .help-cards {
    flex-direction: column;
  }
  .help-cards-card {
    width: 17rem;
  }
  /* me css */
  .me {
    grid-template-rows: repeat(7, 0.5fr);
    grid-template-columns: repeat(2, 0.5fr);
    grid-template-areas:
      "made with"
      "pic pic"
      "name name"
      "bio bio"
      "btn1 btn1"
      "btn2 btn2"
      "btn3 btn3";
    /* justify-content: center; */
    grid-gap: 1.3em 0;
    margin-bottom: 0;
  }
  .me h5 {
    font-size: 1rem;
  }
  .me h3 {
    font-size: 1.5rem;
  }
  .me h4 {
    font-size: 2rem;
    width: 100%;
    text-align: center;
  }
  .me img {
    height: 10rem;
    width: 10rem;
  }
  .me p {
    font-size: 1.4rem;
    width: 70%;
  }
  .me-btn {
    font-size: 1.5rem;
    width: 10em;
    padding: 0.5rem;
  }
  /* foot css */
  footer {
    height: 15rem;
    flex-direction: column;
    padding: 0;
    grid-gap: 1em;
    gap: 1em;
  }
  .foot-item {
    text-align: center;
  }
}

* {
  font-family: "Shanti", sans-serif;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

.container {
  padding: 2rem 1em 0 1em;
  display: flex;
  height: auto;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: var(--white);
}

